

















import Vue from 'vue'
import { authMapper } from '@/store/modules/auth.module'

export default Vue.extend({
  name: 'Dashboard',
  computed: {
    ...authMapper.mapState({
      user: 'user',
      institution: 'institution',
    }),
    username(): string {
      return this.user?.username || ''
    },
  },
  data() {
    return {}
  },
  methods: {},
})
