















































































































































































import Vue from 'vue'
import { InstitutionType, UserRole } from '@/models'
import { authMapper } from '@/store/modules/auth.module'
import ChangePasswordForm from '@/components/ChangePasswordForm.vue'
import ChangeInstitutionForm from '@/components/ChangeInstitutionForm.vue'
import AddOrChangeUserForm from '@/components/AddOrChangeUserForm.vue'
import { UserDTO } from '@/api/SwaggerApi'

const tableColumns = [
  {
    title: 'Vorname',
    key: 'firstName',
    dataIndex: 'firstName',
  },
  {
    title: 'Nachname',
    key: 'lastName',
    dataIndex: 'lastName',
  },
  {
    title: 'Benutzername',
    key: 'username',
    dataIndex: 'username',
  },
  {
    title: 'Rolle',
    key: 'authorities',
    dataIndex: 'authorities',
    scopedSlots: { customRender: 'authorities' },
  },
  {
    title: 'Aktionen',
    key: 'id',
    dataIndex: 'id',
    scopedSlots: { customRender: 'operation' },
  },
]

const roleMapping: { [key: string]: string } = {
  USER_ROLE_ADMIN: 'Admin',
  USER_ROLE_REGULAR: 'Normal',
  ROLE_LABORATORY: 'Labor',
  ROLE_TEST_SITE: 'Teststation',
  ROLE_CLINIC: 'Klinik',
  ROLE_DOCTORS_OFFICE: 'Arzt',
  ROLE_GOVERNMENT_AGENCY: 'Gesundheitsbehrde',
  ROLE_DEPARTMENT_OF_HEALTH: 'Gesundheitsamt',
}

const typeMapping: { [key in InstitutionType]: string } = {
  LABORATORY: 'Labor',
  TEST_SITE: 'Teststation',
  CLINIC: 'Klinik',
  DOCTORS_OFFICE: 'Arzt',
  GOVERNMENT_AGENCY: 'Gesundheitsbehrde',
  DEPARTMENT_OF_HEALTH: 'Gesundheitsamt',
}

export default Vue.extend({
  name: 'Account',
  created() {
    this.setIsAdmin()
    this.getInstitutionUsers()
  },
  data() {
    return {
      tableColumns,
      roleMapping,
      typeMapping,
      registerUserForm: this.$form.createForm(this),
      showChangePasswordForm: false,
      showChangeInstitutionForm: false,
      showAddOrChangeUserForm: false,
      addOrChangeUser: {},
      isAdmin: false,
    }
  },
  components: {
    ChangePasswordForm,
    ChangeInstitutionForm,
    AddOrChangeUserForm,
  },
  computed: {
    ...authMapper.mapState({
      user: 'user',
      institution: 'institution',
      institutionUsers: 'institutionUsers',
    }),
    username(): string {
      return this.user?.username || ''
    },
  },
  watch: {
    user() {
      this.setIsAdmin()
    },
  },
  methods: {
    ...authMapper.mapActions({
      registerUser: 'registerUserForInstitution',
      getInstitutionUsers: 'getInstitutionUsers',
      deleteUserForInstitution: 'deleteUserForInstitution',
    }),
    setIsAdmin() {
      if (this.user && this.user.authorities) {
        this.isAdmin = this.user.authorities.some(
          (authority) => authority.authority === 'USER_ROLE_ADMIN'
        )
      }
    },
    changeUser(user: UserDTO) {
      this.addOrChangeUser = user
      this.showAddOrChangeUserForm = true
    },
    deleteUser(user: UserDTO) {
      if (user.id) {
        this.deleteUserForInstitution(user.id)
          .then(() => {
            this.$notification.success({
              message: 'Benutzer erfolgreich gelscht',
              description: '',
            })
          })
          .catch((error: Error) => {
            this.$notification.error({
              message: 'Benutzer konnte nicht gelscht werden',
              description: error.message,
            })
          })
      }
    },
    addUser() {
      this.addOrChangeUser = {}
      this.showAddOrChangeUserForm = true
    },
  },
})
