

















































































import { Patient } from '@/api/SwaggerApi'
import Api from '@/api'
import Vue from 'vue'
import DateInput from '../components/DateInput.vue'
import PatientInput from '../components/PatientInput.vue'
import moment from 'moment'

interface State {
  form: any // eslint-disable-next-line
  patient?: Patient;
  today: moment.Moment
}

export default Vue.extend({
  name: 'RequestQuarantine',
  components: {
    PatientInput,
    DateInput,
  },
  data(): State {
    return {
      form: this.$form.createForm(this),
      patient: undefined,
      today: moment(),
    }
  },
  computed: {
    givenPatientId(): string | undefined {
      return this.$route.params.patientId
    },
    showPatientInput(): boolean {
      return !this.givenPatientId
    },
  },
  methods: {
    handleSubmit() {
      this.form.validateFields((err: Error, values: any) => {
        // eslint-disable-next-line
        if (err) {
          return
        }
        const request = {
          dateUntil: values.dateUntil.format('YYYY-MM-DD'),
          eventDate: values.eventDate
            ? values.eventDate.format('YYYY-MM-DD')
            : undefined,
          comment: values.comment,
        }
        const patientId = this.givenPatientId
          ? this.givenPatientId
          : values.patientId

        Api.requestQuarantineUsingPost(patientId, request)
          .then((patient) => {
            const h = this.$createElement
            this.$success({
              title: 'Der Quarantnevermerk wurde erfasst.',
              content: h('div', {}, [
                h('div', `Patient: ${patient.firstName} ${patient.lastName}`),
                h(
                  'div',
                  `In Quarantne bis: ${moment(patient.quarantineUntil).format(
                    'DD.MM.YYYY'
                  )}`
                ),
              ]),
            })
            this.form.resetFields()
          })
          .catch((err) => {
            const notification = {
              message: 'Fehler beim hinterlegen des Quarantnevermerks',
              description: err.message,
            }
            this.$notification.error(notification)
          })
      })
    },
  },
})
