

























































































































import Vue from 'vue'
import { authMapper } from '@/store/modules/auth.module'
import { Plz } from '@/util/plz-service'
import PlzInput from '@/components/PlzInput.vue'

export default Vue.extend({
  name: 'Account',
  props: ['visible', 'institution'],
  components: {
    PlzInput,
  },
  data() {
    return {
      form: this.$form.createForm(this, { name: 'institution' }),
    }
  },
  methods: {
    ...authMapper.mapActions({
      updateInstitution: 'updateInstitution',
    }),
    setPLZ(plz: Plz) {
      this.form.setFieldsValue({
        zip: plz.fields.plz,
        city: plz.fields.note,
      })
    },
    save() {
      this.form.validateFields((err: any, values: any) => {
        if (err) {
          console.warn(err)
          return
        }
        this.updateInstitution({
          ...values,
          institutionType: this.institution.institutionType,
          id: this.institution.id,
        })
          .then(() => {
            this.$notification.success({
              message: 'Institution erfolgreich gendert',
              description: '',
            })
            this.form.resetFields()
            this.$emit('create')
          })
          .catch((error: Error) => {
            this.$notification.error({
              message: 'Institution konnte nicht gendert werden.',
              description: error.message,
            })
          })
      })
    },
  },
})
