
























































import Vue from 'vue'
import { authMapper } from '@/store/modules/auth.module'

export default Vue.extend({
  name: 'Header',
  computed: {
    ...authMapper.mapState({
      user: 'user',
    }),
    username(): string {
      return this.user?.username || ''
    },
  },
  data() {
    return {
      form: this.$form.createForm(this),
      visible: false,
    }
  },
  methods: {
    ...authMapper.mapActions({
      logout: 'logout',
    }),
    onLogout() {
      this.logout()
    },
    routeClicked() {
      this.visible = false
      this.$emit('route-clicked')
    },
  },
})
