





















































































































































































































































import { Column } from 'ant-design-vue/types/table/column'
import Vue from 'vue'
import { Patient, PatientSearchParamsDTO } from '@/api/SwaggerApi'
import { eventTypes } from '@/models/event-types'
import { PatientStatus } from '@/models'
import { downloadCsv } from '@/util/export-service'
import Api from '@/api'
import moment from 'moment'
import IndexPatientTableCell from '@/components/IndexPatientTableCell.vue'

const columnsSchema: Partial<Column>[] = [
  {
    title: '',
    dataIndex: 'operation',
    scopedSlots: {
      customRender: ['operation'],
    },
  },
  {
    title: 'Name',
    key: 'lastName',
    scopedSlots: {
      customRender: ['name'],
    },
  },
  {
    title: 'Geschlecht',
    dataIndex: 'gender',
    key: 'gender',
    scopedSlots: {
      customRender: 'gender',
    },
  },
  {
    title: 'Status',
    dataIndex: 'patientStatus',
    key: 'patientStatus',
    scopedSlots: {
      customRender: 'patientStatus',
    },
    sorter: true,
  },
  {
    title: 'Stadt',
    key: 'city',
    scopedSlots: {
      customRender: 'city',
    },
  },
  {
    title: 'Alter',
    dataIndex: 'dateOfBirth',
    key: 'age',
    scopedSlots: {
      customRender: 'age',
    },
  },
  {
    title: 'Indexpatient',
    dataIndex: 'id',
    key: 'indexpatient',
    scopedSlots: {
      customRender: 'indexpatient',
    },
  },
  {
    title: 'ID',
    dataIndex: 'id',
    key: 'id',
    defaultSortOrder: 'ascend',
  },
]

interface SimpleForm {
  query: string
  order: string
  orderBy: string
  offsetPage: number
  pageSize: number
}

interface State {
  form: SimpleForm
  advancedForm: Partial<PatientSearchParamsDTO>
  quarantineSelection: string
  currentPatients: Patient[]

  [key: string]: any
}

export default Vue.extend({
  name: 'PatientList',
  components: {
    IndexPatientTableCell,
  },
  data(): State {
    return {
      form: {
        query: '',
        order: 'asc',
        orderBy: 'lastName',
        offsetPage: 0,
        pageSize: 10,
      },
      advancedForm: {
        gender: '',
        city: '',
        email: '',
        phoneNumber: '',
        street: '',
        houseNumber: '',
        zip: '',
        insuranceCompany: '',
        insuranceMembershipNumber: '',
        doctorId: '',
        laboratoryId: '',
        firstName: '',
        lastName: '',
        patientStatus: undefined,
        quarantineStatus: undefined,
        id: '',
      },
      quarantineSelection: '',
      content: '',
      count: 10,
      currentPage: 1, // Starts at 1
      columnsSchema,
      data: [], // data
      showAdvancedSearch: false,
      eventTypes: eventTypes,
      currentPatients: [],
    }
  },
  watch: {
    currentPage() {
      this.loadPage()
    },
    '$route.query.query'() {
      this.loadAfterUrlChange()
    },
  },
  created() {
    this.indexPatientMap = new Map()
    this.loadAfterUrlChange()
  },
  methods: {
    loadAfterUrlChange() {
      const query = this.$route.query.query
      if (query) {
        this.form.query = query.toString()
      }
      this.loadPage()
    },
    handleSearch() {
      this.currentPage = 1
      this.loadPage()
    },
    onShowSizeChange(current: number, pageSize: number) {
      this.currentPage = current
      this.advancedForm.pageSize = pageSize
      this.loadPage()
    },
    loadPage() {
      this.form.offsetPage = this.currentPage - 1
      let countPromise
      let queryPromise
      if (this.showAdvancedSearch) {
        const formValues = { ...this.form, ...this.advancedForm }

        if (!formValues.patientStatus) {
          // Backend fails on empty string
          formValues.patientStatus = undefined
        }
        formValues.quarantineStatus = this.getQuarantineSelection() as PatientStatus[]

        countPromise = Api.countQueryPatientsUsingPost(formValues)
        queryPromise = Api.queryPatientsUsingPost(formValues)
      } else {
        const query = this.form.query
        countPromise = Api.countQueryPatientsSimpleUsingGet({ query })
        queryPromise = Api.queryPatientsSimpleUsingPost({ ...this.form })
      }

      countPromise.then((count) => {
        this.count = count
      })
      queryPromise
        .then((result: Patient[]) => {
          this.currentPatients = result
        })
        .catch((error) => {
          console.error(error)
          const notification = {
            message: 'Fehler beim Laden der Patientendaten.',
            description: error.message,
          }
          this.$notification.error(notification)
        })
    },
    toggleAdvancedSearch() {
      this.showAdvancedSearch = !this.showAdvancedSearch
    },
    downloadPatients() {
      this.form.offsetPage = this.currentPage - 1
      let countPromise
      let formValues: any
      if (this.showAdvancedSearch) {
        formValues = { ...this.form, ...this.advancedForm }
        if (!formValues.patientStatus) {
          // Backend fails on empty string
          delete formValues.patientStatus
        }
        formValues.quarantineStatus = this.getQuarantineSelection()
        countPromise = Api.countQueryPatientsUsingPost(formValues)
      } else {
        formValues = { ...this.form }
        const query = this.form.query
        countPromise = Api.countQueryPatientsSimpleUsingGet({ query })
      }

      countPromise.then((count) => {
        // Download all data that applies to the current filter
        formValues.offsetPage = 0
        formValues.pageSize = count

        let queryPromise: Promise<Patient[]>
        if (this.showAdvancedSearch) {
          queryPromise = Api.queryPatientsUsingPost(formValues)
        } else {
          queryPromise = Api.queryPatientsSimpleUsingPost(formValues)
        }

        queryPromise
          .then((result) => {
            const header =
              'ID;Vorname;Nachname;Geschlecht;Status;Geburtsdatum;Stadt;E-Mail;Telefonnummer;' +
              'Strae;Hausnummer;Stadt;Versicherung;Versichertennummer'
            const patients = result
              .map(
                (patient: Patient) =>
                  `${patient.id};${patient.firstName};${patient.lastName};${patient.gender};${patient.patientStatus};` +
                  `${patient.dateOfBirth};${patient.city};${patient.email};${patient.phoneNumber};${patient.street};` +
                  `${patient.houseNumber};${patient.city};${patient.insuranceCompany};${patient.insuranceMembershipNumber}`
              )
              .join('\n')
            const filename =
              moment().format('YYYY_MM_DD') + '_patienten_export.csv'
            downloadCsv(header + '\n' + patients, filename)
          })
          .catch((error: Error) => {
            console.error(error)
            const notification = {
              message: 'Fehler beim Laden der Patientendaten.',
              description: error.message,
            }
            this.$notification.error(notification)
          })
      })
    },
    handleTableChange(pagination: any, filters: any, sorter: any) {
      const sortKey = sorter.field ? sorter.field : 'lastName'
      let sortOrder = 'asc'
      if (sorter.order === 'descend') {
        sortOrder = 'desc'
      }
      this.form.order = sortOrder
      this.form.orderBy = sortKey
      this.loadPage()
    },
    handlePatientClick(patient: Patient) {
      if (patient.id) {
        this.$router.push({
          name: 'patient-detail',
          params: { id: patient.id },
        })
      }
    },
    customRow(record: Patient) {
      return {
        on: {
          dblclick: () => this.handlePatientClick(record),
        },
      }
    },
    getQuarantineSelection(): (PatientStatus | null)[] {
      if (!this.quarantineSelection) {
        return []
      }
      if (this.quarantineSelection === 'NO_SELECTION') {
        return [
          'QUARANTINE_RELEASED',
          'QUARANTINE_PROFESSIONBAN_RELEASED',
          null,
        ]
      } else {
        return [this.quarantineSelection as PatientStatus]
      }
    },
    moment,
  },
})
